import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
export default {
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "process_code",
      //   
      searchText: "",
      // 
      columnDefs: [{
        field: "process_code",
        headerName: "",
        flex: 1
      }, {
        field: "process_name",
        headerName: "",
        flex: 1
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '  .',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.procList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    async procList() {
      await axios.get('/api/admin/processAll').then(res => {
        console.log('', res.data);
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchProcess() {
      axios.get('/api/admin/processflow/:prodno', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    clicked(event) {
      this.$emit('selectProc', event.data);
      this.close();
    }
  }
};