import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.map.js";
import { AgGridVue } from 'ag-grid-vue3';
import axios from 'axios';
import Swal from 'sweetalert2';
import ProcModal from '@/components/modal/ProcModal.vue';
import { VueFlow, useVueFlow } from '@vue-flow/core';
import { Background } from '@vue-flow/background';
import { ControlButton, Controls } from '@vue-flow/controls';
import { MiniMap } from '@vue-flow/minimap';
export default {
  components: {
    AgGridVue,
    ProcModal,
    Swal,
    VueFlow,
    Background,
    Controls,
    ControlButton,
    MiniMap
  },
  data() {
    return {
      // 
      nodes: [],
      edges: [],
      vueFlowInstance: null,
      // VueFlow  

      rowData: [{
        prod_code: "",
        prod_name: ""
      }],
      columnDefs: [{
        field: 'prod_code',
        headerName: '',
        flex: 1
      }, {
        field: 'prod_name',
        headerName: '',
        flex: 1
      }],
      gridOptions1: {
        domLayout: "autoHeight",
        //   hight
        pagination: true,
        paginationPageSize: 7,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '  .',
        defaultColDef: {
          suppressMovable: true,
          //   
          resizable: false,
          //    
          sortable: false,
          //  
          onGridReady: function (event) {
            event.api.sizeColumnsToFit();
          }
        }
      },
      rowData2: [{
        prod_code: "",
        prod_name: "",
        process_code: "",
        process_name: ""
      }],
      columnDefs2: [{
        field: 'drag',
        headerName: '',
        flex: 1,
        rowDrag: true
      }, {
        headerCheckboxSelection: true,
        checkboxSelection: true,
        width: 50
      }, {
        field: 'prod_code',
        headerName: '',
        flex: 3
      }, {
        field: 'prod_name',
        headerName: '',
        flex: 4
      }, {
        field: 'process_code',
        headerName: '',
        flex: 3
      }, {
        field: 'process_name',
        headerName: '',
        flex: 4
      }],
      gridOptions2: {
        rowDragManaged: true,
        //     
        animateRows: true,
        //     
        domLayout: "autoHeight",
        //   hight
        suppressRowClickSelection: true,
        //	     
        overlayNoRowsTemplate: '  .',
        defaultColDef: {
          suppressMovable: true,
          //   
          resizable: false,
          //    
          sortable: false,
          //  
          onGridReady: function (event) {
            event.api.sizeColumnsToFit();
          }
        }
      },
      showProcModal: false,
      //  
      selectedSecondIndex: null,
      prodIndex: null
    };
  },
  mounted() {
    this.ProcData();
    // VueFlow  
    this.vueFlowInstance = useVueFlow();
  },
  methods: {
    async ProcData() {
      try {
        const response = await axios.get('/api/admin/processflow');
        this.rowData = response.data;
      } catch (err) {
        console.error('  :', err);
      }
    },
    async prodCellClicked(event) {
      let proc = event.data.prod_code;
      this.prodIndex = event.rowIndex;
      await axios.get('/api/admin/processflow/' + proc).then(res => {
        console.log(res);
        this.rowData2 = res.data;
        this.rowData2 = [...this.rowData2];
      }).catch(error => {
        console.error(error);
      });
      console.log(event);
      //
      this.generateFlowDiagram();
    },
    //
    addRow() {
      if (this.prodIndex == null) {
        Swal.fire({
          title: '',
          text: ' ',
          icon: 'error',
          confirmButtonText: ''
        });
        return;
      }
      this.rowData2.push({
        prod_code: this.rowData[this.prodIndex].prod_code,
        prod_name: this.rowData[this.prodIndex].prod_name,
        process_code: "",
        process_name: ""
      });

      //    AG Grid  
      this.rowData2 = [...this.rowData2];
      //  
      this.generateFlowDiagram();
    },
    //
    deleteRow() {
      const selectedNodes = this.$refs.secondGrid.api.getSelectedNodes();
      const selectedData = selectedNodes.map(node => node.data);

      // rowData2     
      this.rowData2 = this.rowData2.filter(row => !selectedData.includes(row));

      //  
      this.generateFlowDiagram();
    },
    //   
    procCellClicked(parmas) {
      if (parmas.colDef.field == "process_code" || parmas.colDef.field == "process_name") {
        this.selectedSecondIndex = parmas.rowIndex;
        this.showProcModal = true;
        console.log('', parmas);
      }
    },
    //   
    procSelected(proc) {
      this.rowData2[this.selectedSecondIndex].process_code = proc.process_code;
      this.rowData2[this.selectedSecondIndex].process_name = proc.process_name;
      //    AG Grid  
      this.rowData2 = [...this.rowData2];

      //  
      this.generateFlowDiagram();
    },
    //
    async proSave() {
      // validation
      for (let data of this.rowData2) {
        console.log(data);
        if (data.prod_code == '' || data.prod_name == '' || data.process_code == '' || data.process_name == '') {
          Swal.fire({
            title: '',
            text: '  ',
            icon: 'error',
            confirmButtonText: ''
          });
          return;
        }
      }
      const res = axios.post('/api/admin/procsave', {
        insertProc: this.rowData2,
        prodFlow: this.rowData[this.prodIndex].prod_code
      }).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: ' ',
            text: '  .',
            icon: 'success',
            confirmButtonText: ''
          });
        } else {
          Swal.fire({
            title: ' ',
            text: ' ..',
            icon: 'error',
            confirmButtonText: ''
          });
        }
      }).catch(error => {
        console.error(error);
        Swal.fire({
          title: ' ',
          text: '   ..',
          icon: 'error',
          confirmButtonText: ''
        });
        return;
      });
      this.rowData2 = [{
        prod_code: "",
        prod_name: "",
        process_code: "",
        process_name: ""
      }];
      this.ProcData();
    },
    //
    onRowDragEnd(event) {
      const updatedData = [];
      event.api.forEachNodeAfterFilterAndSort(node => {
        updatedData.push(node.data);
      });
      this.rowData2 = [...updatedData];

      //  
      this.generateFlowDiagram();
    },
    // 
    generateFlowDiagram() {
      //
      const newNodes = this.rowData2.map((item, index) => ({
        // ID
        id: `${item.process_code || 'node'}-${index}`,
        //
        data: {
          label: item.process_name?.slice(0, 10) || ` ${index + 1}`
        },
        // 
        position: {
          x: 20 + 150 * index,
          y: 10 + 100 * index
        },
        type: index == 0 ? 'input' : index === this.rowData2.length - 1 ? 'output' : 'default',
        class: 'my-custom-node-class',
        //
        style: {
          backgroundColor: '#DA942B',
          color: 'white',
          border: '1px solid #DA942B',
          borderRadius: '30px',
          boxShadow: '0 2px 6px rgba(0,0,0,0.2)',
          width: '150px',
          height: '40px'
        }
      }));

      //  :     
      const newEdges = this.rowData2.slice(1).map((item, index) => ({
        id: `edge-${index}`,
        source: `${this.rowData2[index].process_code || 'node'}-${index}`,
        target: `${item.process_code || 'node'}-${index + 1}`,
        //
        type: 'step',
        //  
        animated: true,
        //
        style: {
          stroke: '#673AB7'
        },
        // 
        label: ` ${index + 1} -> ${index + 2}`,
        // 
        labelBgStyle: {
          fill: '#f5f5f5'
        },
        labelStyle: {
          fill: '#333'
        }
      }));
      this.nodes = [...newNodes];
      this.edges = [...newEdges];
    },
    // 
    resetList() {
      this.rowData2 = [];
      this.nodes = [];
      this.edges = [];
    }
  }
};